12., 13., 10.); \n    cout << \"Volume of Box1 : \" << box1.getVolume() << endl;\n    cout << \"Volume of Box2 : \" << box2.getVolume() << endl;\n    Box box3 = box1 + box2;\n    cout << \"Volume of Box3 : \" << box3.getVolume() << endl;\n    return 0;\n}")])]),_c('ul',{staticClass:"ne-ul"},[_c('li',{attrs:{"id":"u98f7676f"}},[_c('span',{staticClass:"ne-text",staticStyle:{"font-size":"16px"}},[_vm._v(" 第12-14行重载了操作符 ")]),_c('code',{staticClass:"ne-code"},[_c('span',{staticClass:"ne-text",staticStyle:{"font-size":"16px"}},[_vm._v(" + ")])]),_c('span',{staticClass:"ne-text",staticStyle:{"font-size":"16px"}},[_vm._v(" ，两个操作数分别为当前对象 ")]),_c('code',{staticClass:"ne-code"},[_c('span',{staticClass:"ne-text",staticStyle:{"font-size":"16px"}},[_vm._v(" this ")])]),_c('span',{staticClass:"ne-text",staticStyle:{"font-size":"16px"}},[_vm._v(" 和参数 ")]),_c('code',{staticClass:"ne-code"},[_c('span',{staticClass:"ne-text",staticStyle:{"font-size":"16px"}},[_vm._v(" b ")])]),_c('span',{staticClass:"ne-text",staticStyle:{"font-size":"16px"}},[_vm._v(" ，当第24行对 ")]),_c('code',{staticClass:"ne-code"},[_c('span',{staticClass:"ne-text",staticStyle:{"font-size":"16px"}},[_vm._v(" Box ")])]),_c('span',{staticClass:"ne-text",staticStyle:{"font-size":"16px"}},[_vm._v(" 对象执行 ")]),_c('code',{staticClass:"ne-code"},[_c('span',{staticClass:"ne-text",staticStyle:{"font-size":"16px"}},[_vm._v(" + ")])]),_c('span',{staticClass:"ne-text",staticStyle:{"font-size":"16px"}},[_vm._v(" 操作时，就会调用这个函数。C++中提供的操作符都可以进行重载。 ")])]),_c('li',{attrs:{"id":"u6cf979f8"}},[_c('span',{staticClass:"ne-text",staticStyle:{"font-size":"16px"}},[_vm._v(" 通常对操作符重载时，参数都使用const，防止用户对其进行修改，引用可以减少传值过程中对象被复制从而浪费时间和空间。 ")])])]),_c('h3',{attrs:{"id":"WLtg1"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 知识点：T771 ")])]),_c('table',{staticClass:"ne-table",staticStyle:{"width":"100%"},attrs:{"border":"1","id":"A9sdu"}},[_c('tbody',[_c('tr',{staticStyle:{"height":"33px"}},[_c('td',{attrs:{"width":"58"}},[_c('p',{staticClass:"ne-p",attrs