,[_c('a',{staticClass:"ne-link",attrs:{"data-href":"https://www.qingline.net/practice-list/practice?question_bank_id=13997&id=161755","href":"https://www.qingline.net/practice-list/practice?question_bank_id=13997&id=161755","target":"_blank"}},[_c('span',{staticClass:"ne-text",staticStyle:{"font-size":"16px"}},[_vm._v(" (LX701) ")])]),_c('span',{staticClass:"ne-text",staticStyle:{"font-size":"16px"}})])]),_c('td',{attrs:{"width":"70"}}),_c('td',{attrs:{"width":"67"}})])])]),_c('h1',{attrs:{"id":"aHidZ"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 9. 综合练习-构建链表 ")])]),_c('p',{staticClass:"ne-p",staticStyle:{"text-indent":"2em"},attrs:{"id":"uedd452e7"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 本节通过构建链表，练习类和对象的构造，动态对象和this指针、封装和操作符重载。更重要的是理解链表这种特殊的数据结构。链表可以形成一个序列，但是序列中的每个节点的存储空间都是不连续的。这样可以更加方便的插入和删除，但是不能进行随机访问。 ")])]),_c('p',{staticClass:"ne-p",staticStyle:{"text-indent":"2em"},attrs:{"id":"u614e3334"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 首先，构建一个节点类，每个节点除了保存数据外，更重要的是形成一个指针，用与指向下一个节点，这样才能形成序列。 ")])]),_c('pre',{staticClass:"ne-codeblock language-cpp",attrs:{"data-language":"cpp","id":"qOfzC"}},[_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("代码7.10")]),_vm._v("构建节点类\n "),_c('code',[_vm._v("class Node\n{\npublic:\n    int val;\t\t\t\t\t\t\t\t//存储数据，此处以int为例\n    Node *next;\t\t\t\t\t\t\t\t//用于存储下一个节点\n    Node(int val, Node *next = nullptr)\n    {\n        this->val = val;\t\t\t\t\t//成员变量和形参同名，用this指针进行区分\n        this->next = next;\n    }\n};")])]),_c('p',{staticClass:"ne-p",staticStyle:{"text-indent":"2em"},attrs:{"id":"u90735aea"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 其次构建一个迭代器，用于遍历节点。这里最重要的一个操作是将节点封装成private，外部无法对其直接访问。因为链表的存储空间不连续，因此不能使用大于小于等比较，但是要重载等于和不等于操作。任何迭代器都要支持自加和取值操作。从以下代码可以看出，迭代器就是一个封装后的指针，但是因为进行了封装，用户不能直接访问指针，因此只能使用Iterator中提供的方法对指针进行访问，限定了用户的访问权限，达成了安全访问。 ")])]),_c('pre',{staticClass:"ne-codeblock language-cpp",attrs:{"data-language":"cpp","id":"qVzkh"}},[_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("代码7.11")]),_vm._v("构建迭代器\n "),_c('code',[_vm._v("class Iterator{\nprivate:\n    Node* ptr;\t\t\t\t\t//构造私有节点指针\npublic:\n    Iterator(Node* node){\n        ptr = node;\n    }\n    // 拷贝赋值函数\n    Iterator& operator=(const Iterator& it) {\n        ptr = it.ptr;\t\t\t//对私有节点指针进行赋值\n        return *this;\t\t\t//返回自己\n    }\n    // 等于运算符\n    bool operator==(const Iterator& it) const {\n        return ptr == it.ptr;\t//对私有节点指针进行比较\n    }\n    // 不等于运算符\n    bool operator!=(const Iterator& it) const {\n        return ptr != it.ptr;\t//对私有节点指针进行比较\n    }\n    //前缀自加\n    Iterator& operator++() {\n        ptr = ptr->next;\t\t//让私有指针指向下一个节点\n        return *this;\t\t\t//返回自己\n    }\n    //后缀自加\n    Iterator operator++(int) {\n        Iterator tmp = *this;\t//得到自己\n        ++(*this);\t\t\t\t//执行前缀自加操作，让私有指针指向下一个节点\n        return tmp;\n    }\n    int operator*(){\n        return ptr->val;\t\t//取值操作\n    }\n};")])]),_c('p',{staticClass:"ne-p",staticStyle:{"text-indent":"2em"},attrs:{"id":"u2a49c1b6"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 然后构建一个链表类，其中的head始终指向头节点。begin和end操作能够得到迭代器。然后分别实现了尾部添加、插入和删除操作。 ")])]),_c('pre',{staticClass:"ne-codeblock language-cpp",attrs:{"data-language":"cpp","id":"uDSYC"}},[_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("代码7.12")]),_vm._v("构建链表类\n "),_c('code',[_vm._v("class List{\nprivate:\n    Node* head;\npublic:\n    List(){\n        head = nullptr;\n    }\n    Iterator begin(){\n        return Iterator(this->head);\t\t//由头结点封装的迭代器\n    }\n    Iterator end(){\n        return Iterator(nullptr);\t\t\t//尾结点是空指针\n    }\n    void push_back(int val){\n        Node* node = new Node(val);\t\t\t//构造一个新节点\n        if(!head)\t\t\t\t\t\t\t//如果链表为空\n            head = node;\t\t\t\t\t//头结点就是新节点，构建了只有一个节点的链表\n        else{\n            Node* ptr = head;\n            while(ptr->next)\t\t\t\t//如果没有到尾部\n                ptr = ptr->next;\t\t\t//转入下一个节点\n            ptr->next = node;\t\t\t\t//新节点作为尾节点\n        }\n    }\n    bool insert(const Iterator& it,int val){\n        Node* tmp = head;\n        while(Iterator(tmp)!=end() && Iterator(tmp->next)!=it)\t//以迭代器形式比较\n            tmp = tmp->next;\t\t\t\t//转入下一个节点\n        if(Iterator(tmp)==end())  return false;\t//没有找到迭代器指向的位置\n        Node* node = new Node(val);\t\t\t//构造一个新节点\n        node->next = tmp->next;\t\t\t\t//新节点和下一个节点链接\n        tmp->next = node;\t\t\t\t\t//新节点和上一个节点链接，注意和上一行顺序不能变\n        return true;\n    }\n    bool erase(const Iterator& it){\n        Node* tmp = head;\n        while(Iterator(tmp)!=end() && Iterator(tmp->next)!=it)\t//以迭代器形式比较\n            tmp = tmp->next;\t\t\t\t//转入下一个节点\n        if(Iterator(tmp)==end())  return false;\t//没有找到迭代器指向的位置\n        Node* node = tmp->next;\t\t\t\t//保留要删除节点的指针\n        tmp->next = node->next;\t\t\t\t//让上一个节点直接执行下一个节点，形成删除\n        delete node;\t\t\t\t\t\t//删除节点\n        return true;\n    }\n};")])]),_c('p',{staticClass:"ne-p",staticStyle:{"text-indent":"2em"},attrs:{"id":"u0bb2407f"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 最后，利用以上的定义，演示了使用方法。这里可以看到，只能通过迭代器访问节点，用户不能直接对节点进行操作，从而实现了对链表的安全访问。因为List支持了 ")]),_c('code',{staticClass:"ne-code"},[_c('span',{staticClass:"ne-text"},[_vm._v(" begin ")])]),_c('span',{staticClass:"ne-text"},[_vm._v(" 、 ")]),_c('code',{staticClass:"ne-code"},[_c('span',{staticClass:"ne-text"},[_vm._v(" end ")])]),_c('span',{staticClass:"ne-text"},[_vm._v(" 和迭代器，因此可以执行 ")]),_c('code',{staticClass:"ne-code"},[_c('span',{staticClass:"ne-text"},[_vm._v(" for(auto 变量:容器) ")])]),_c('span',{staticClass:"ne-text"},[_vm._v(" 操作，进一步说明迭代器可以实现算法和具体数据的分离。 ")])]),_c('pre',{staticClass:"ne-codeblock language-cpp",attrs:{"data-language":"cpp","id":"Jf9hQ"}},[_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("代码7.13")]),_vm._v("演示链表类的使用\n "),_c('code',[_vm._v("#include<iostream>\nusing namespace std;\n\nint main(){\n    int m;\n    cin>>m;\n    List ls;\n    for(int i=0;i<m;i++)\n        ls.push_back(i+1);\t\t//构建链表的数据\n    for(auto it = ls.begin();it!=ls.end();it++)\n        cout<<*it<<' ';\n    cout<<endl